.slick-slide img {
  object-fit: cover;
}

.slick-dots {
  bottom: 20px !important; 
  left: 0 !important;
  right: 0 !important;
  gap: 20px !important;
  margin: 0 auto !important;
  display: flex !important;
  justify-content: center !important;
}
.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}

.pagination button {
  width: 40px;
  height: 40px;
  font-family: inherit;
  font-weight: 600;
  font-size: 16px;
  margin: 0 10px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #101010;
  color: #eee;
  border-color: #eee;
}

.pagination button.active {
  font-weight: 900;
  border-color: #101010;
  background: #ffe400;
  color: #101010;
}


.slick-dots li button:before {
  color: #2699fb !important;
  border: 1px solid #2699fb !important;
  border-radius: 50% !important;
  background: white !important;
}

