.border-btn{
  color: #fff;
  background: none;
  text-transform: normal;
  padding: 17px 39px  !important;
  margin-top: 30px;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  position: relative;
  transition: color 0.4s linear;
  position: relative;
  overflow: hidden;
  border-radius: 30px;
  z-index: 1;
  border: 1px solid #ffffff;
  transition: all .3s
}

.border-btn::before {
  border: 1px solid transparent;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 101%;
  height: 101%;
  background: #fff;
  z-index: -1;
  transition: transform 0.5s;
  transition-timing-function: ease;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  transform: scaleY(0);
  border-radius: 0
}

.border-btn:hover::before {
  transform: scaleY(1);
  border: 1px solid transparent;
  
}

.border-btn:hover {
  color: #FF1616
}

